/* 右侧栏 */
.rightcolumn {
    float: left;
    width: 25%;
    background-color: #f1f1f1;
    padding-left: 20px;
}
/* 文章卡片效果 */
.card {
    background-color: white;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 1px;
    padding-bottom: 10px;
    margin-top: 20px;
}

/* 分析结论部分 */
.conclusion {
    background-color: rgba(230, 230, 230, 0.753);
    width: 100%;
    padding: 20px;
    overflow: scroll;
}

/* table部分*/
.myTable {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
}

.myTable td,
.myTable th {
    border: 1px solid #ddd;
    padding: 8px;
}

.myTable tr:nth-child(even) {
    background-color: #d1d1d1;
}

.myTable tr:hover {
    background-color: rgb(146, 146, 146);
}

.myTable th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #292e29;
    color: white;
}

/* 响应式布局 - 屏幕尺寸小于 800px 时，两列布局改为上下布局 */
@media screen and (max-width: 800px) {
    .rightcolumn {
        width: 100%;
        padding: 0;
    }
}