/* 导航条 */
.topnav {
    overflow: hidden;
    background-color: #333;
}

/* 导航条链接 */
.topnav button {
    float: left;
    display: block;
    margin-right: 10px;
    margin-left: 2px;
    margin-top: 5px;
    margin-bottom: 5px;
    text-align: center;
    padding: 10px 10px;
    text-decoration: none;
}

.topnav button:hover {
    cursor: pointer;
    background-color:bisque;
}
