/* Left column */
.leftcolumn {
    float: left;
    width: 75%;
}
.map {
    background-color: rgba(230, 230, 230, 0.753);
    margin-top: 20px;
    margin-bottom: 20px;
    width: 100%;
    padding: 20px;
}

/* 响应式布局 - 屏幕尺寸小于 800px 时，两列布局改为上下布局 */
@media screen and (max-width: 800px) {

    .leftcolumn {
        width: 100%;
        padding: 0;
    }
}